body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.actions {
  width: 90vw;
  height: 100px;
  background-color: white;
  position: relative;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
  z-index: 1;
}

.actions__big-image-album {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  border: 8px white solid;
  position: absolute;
  z-index: 2;
  background-position: center;
  background-size: cover;
}

.actions__big-image-album.active {
  -webkit-animation: rotateAlbumArt 3s linear 0s infinite forwards;
          animation: rotateAlbumArt 3s linear 0s infinite forwards;
}

.actions__image-container {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  border: 8px white solid;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -40px;
  left: 40px;
  z-index: 1;
}

.actions__small-image-album {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 2px white solid;
  background-color: rgb(214, 222, 231);
  z-index: 3;
}

.actions__selection-container {
  width: 200px;
  height: 100px;
  position: absolute;
  right: 40px;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.actions__rewind-icon, .actions__forward-icon {
  font-size: 40px;
  cursor: pointer;
}

.actions__play-icon, .actions__pause-icon {
  font-size: 50px;
  cursor: pointer;
}

.actions__play-button, .actions__pause-button, .actions__rewind-button,
.actions__forward-button {
  width: 80px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 64, 102);
}

.actions__play-button:hover, .actions__pause-button:hover, .actions__rewind-button:hover,
.actions__forward-button:hover {
  background-color: #04a1fb;
  color: white !important;
}

@-webkit-keyframes rotateAlbumArt {
  0% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

@keyframes rotateAlbumArt {
  0% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}

.song-information {
  height: 100px;
  background-color: rgb(0, 64, 102);
  position: absolute;
  top: -100px;
  right: 15px;
  left: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.song-information.enabled {
  -webkit-animation: 0.2s ease-in moveInTop;
          animation: 0.2s ease-in moveInTop;
}

.song-information.disabled {
  -webkit-animation: 0.2s ease-in moveInBottom;
          animation: 0.2s ease-in moveInBottom;
  top: 0;
}

.firstTime {
  display: none;
}

.song-information__data-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  right: 15px;
  left: 180px;
}

.song-information__song-name {
  color: #04a1fb;
  font-size: 17px;
  font-weight: bold;
}

.song-information__artist {
  color: #acaebd;
  font-size: 13px;
}

.song-information__time-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #04a1fb;
  font-size: 11px;
  margin: 13px 0 3px 0;
  font-weight: bold;
}

.song-information__time-bar {
  height: 4px;
  background-color: white;
  border-radius: 4px;
  width: 100%;
  cursor: pointer;
}

.song-information__progressive-time {
  height: 4px;
  background-color: #04a1fb;
  border-radius: 4px;
  z-index: 1;
}

@-webkit-keyframes moveInTop {

  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes moveInTop {

  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes moveInBottom {

  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes moveInBottom {

  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.sound-player {
  width: 90vw;
  height: 100px;
  background-color: white;
  position: relative;
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}

.App {
  width: 100vw;
  height: 100vh;
  background-color: #B4E1E7;
  display: flex;
  justify-content: center;
  align-items: center;
}
